<template>
  <v-select
      label="Server"
      :items="servers"
      variant="solo"
      density="compact"
      v-model="widget.properties.control.endpointUrl"
      item-title="name"
      item-value="endpoint"
  >
  </v-select>
  <v-text-field variant="solo" density="compact" label="System Id" v-model="widget.properties.control.systemId"></v-text-field>
  <v-text-field variant="solo" density="compact" label="Command Stream Id" v-model="widget.properties.control.cmdStreamId"></v-text-field>
</template>

<script>
import {inject} from "vue";
import {useClientStore} from "@/store/client";
import {useRoute} from "vue-router/dist/vue-router";

export default {
  name: "PtzForm",
  setup(props) {
    const widget = inject('widget');

    const clientStore = useClientStore();
    const route = useRoute();
    // check if the dashboard exists
    let {dashboardId} = route.params;
    const servers = clientStore.getServersByDashboard(dashboardId);

    if(!widget.properties) {
      widget.properties = {};
    }
    if(!widget.properties.control) {
      widget.properties.control = {
        endpointUrl: undefined,
        tls: true,
        connectorOpts: {
          username: 'uxs-team',
          password: 'WR6zlso9h#'
        },
        systemId: undefined,
        cmdStreamId: undefined
      };
    }
    widget.supportedLayers = [];
    widget.type = 'PtzControl';
    widget.name = 'ptzControl';
    widget.description= 'PTZ control';
    return { widget, servers };
  }
}
</script>

