<template>
  <div :id="id" class="ptz-container">
    <div class="ptz-zoom">
      <div class="ptz-zoom-in" v-on:click="onZoomClick(zoomIncrement)"><i class="fa fa-plus-circle"
                                                                          aria-hidden="true"></i></div>
      <div class="ptz-zoom-bar"></div>
      <div class="ptz-zoom-out" v-on:click="onZoomClick(-1*zoomIncrement)"><i class="fa fa-minus-circle"
                                                                              aria-hidden="true"></i></div>
    </div>
    <div class="ptz">
      <div tag="0" class='moveUp' v-on:click="onTiltClick(tiltIncrement)" name=""></div>
      <div tag="91" class='moveTopLeft' v-on:click="onTiltPanClick(-1 * tiltIncrement, panIncrement)" name=""></div>
      <div tag="90" class="moveTopRight" v-on:click="onTiltPanClick(tiltIncrement, panIncrement)" name=""></div>
      <div tag="6" class="moveLeft" v-on:click="onPanClick(-1 * panIncrement)" name=""></div>
      <div cmd="ptzReset" class="reset" title="Center" name=""></div>
      <div tag="4" class="moveRight" v-on:click="this.onPanClick(panIncrement)" name=""></div>
      <div tag="93" class="moveBottomLeft" v-on:click="this.onTiltPanClick(-1 * tiltIncrement, -1 * panIncrement)"
           name=""></div>
      <div tag="92" class="moveBottomRight" v-on:click="onTiltPanClick(tiltIncrement, -1 * panIncrement)" name=""></div>
      <div tag="2" class="moveDown" v-on:click="onTiltClick(-1*tiltIncrement)" name=""></div>
    </div>
  </div>
</template>

<script>
import {isDefined, randomUUID} from 'osh-js/core/utils/Utils';
import Systems from "osh-js/core/sweapi/system/Systems";

export default {
  name: "PtzView",
  props: ['widget'],
  setup(props) {
    const id = randomUUID();
    let panIncrement = 10;
    let tiltIncrement = 10;
    let zoomIncrement = 0.1;
    return {id, panIncrement, tiltIncrement, zoomIncrement};
  },
  data() {
    return {
      initSwe: false
    }
  },
  methods: {
    async initSweApi() {
      if (!this.initSwe) {
        const properties = this.widget.properties;
        const systems = new Systems(properties.control);
        const system = await systems.getSystemById(properties.control.systemId);
        this.control = await system.getControlById(properties.control.cmdStreamId);
        this.initSwe = true;
      }
    },
    /**
     * Changes the value on click
     * @private
     * @event
     * @param {Number} value - the new tilt value
     */
    onTiltClick(value) {
      this.onChange(null, value, null, null);
    },
    /**
     * @private
     * @param  {Number} tiltValue - the titl value
     * @param  {Number} panValue -  the panValue value
     * @event
     */
    onTiltPanClick(tiltValue, panValue) {
      this.onChange(tiltValue, panValue, null, null);
    },
    /**
     *
     * @private
     * @param value
     */
    onPanClick(value) {
      this.onChange(value, null, null, null);
    },
    /**
     * @private
     * @param value
     */
    onZoomClick(value) {
      this.onChange(null, null, value, null);
    },
    /**
     * Event caught after clicking onto control buttons.
     * @event
     * @param {Number} rpan -
     * @param {Number} rtilt -
     * @param {Number} rzoom -
     */
    async onChange(rpan, rtilt, rzoom) {
      await this.initSweApi();
      if (isDefined(rpan)) {
        let cmd = {
          params: {
            rpan: rpan
          }
        };
        console.log(JSON.stringify(cmd));
        await this.control.postCommand(JSON.stringify(cmd));
      }
      if (isDefined(rtilt)) {
        let cmd = {
          params: {
            rtilt: rtilt
          }
        };
        console.log(JSON.stringify(cmd));
        await this.control.postCommand(JSON.stringify(cmd));
      }
      if (isDefined(rzoom)) {
        let cmd = {
          params: {
            rzoom: rzoom
          }
        };
        console.log(JSON.stringify(cmd));
        await this.control.postCommand(JSON.stringify(cmd));
      }
    }
  }
}
</script>

